export default "\r\n/* NAVIGATION */\r\n#hk-nav {\r\n  display: block;\r\n  max-width: 1000px;\r\n  width: 100%;\r\n  /* font-family: Georgia, 'Times New Roman', Times, serif; */\r\n  font-family: Merriweather;\r\n  font-size: 0;\r\n  line-height: 0;\r\n  margin: 0 auto;\r\n  padding: 0;\r\n  background-color: #ff9e9e;\r\n  color: #ffffff;\r\n  /* border-radius: 0 0 20px 20px; */\r\n}\r\n#hk-nav ul {\r\n  margin: 0;\r\n  padding: 0;\r\n  overflow: hidden;\r\n}\r\n#hk-nav ul li {\r\n  float: left;\r\n  padding: 0 auto;\r\n  margin: 0 auto;\r\n}\r\n#hk-nav ul li a {\r\n  display: block;\r\n  font-size: 15px;\r\n  line-height: normal;\r\n  text-align: center;\r\n  color: #ffffff;\r\n  padding: 15px 20px;\r\n}\r\n#hk-nav ul li a:hover {\r\n  color: #ff3d3d;\r\n  -webkit-transition: all .3s ease-in-out;\r\n  -moz-transision: all .3s ease-in-out;\r\n  transition: all .3s ease-in-out;\r\n}\r\n@media screen and (max-width: 480px) {\r\n  #hk-nav ul li a {\r\n    margin: 0 10px;\r\n    font-size: 14px;\r\n    padding: 15px 5px;\r\n  }\r\n}\r\n@media screen and (max-width: 340px) {\r\n  #hk-nav ul li a {\r\n    margin: 0 5px;\r\n    font-size: 14px;\r\n  }\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"